<template>
  <div class="accountDetailCon">
    <div class="backIcon" @click="prev()"></div>
    <div class="bg"></div>
    <div class="select" @click="select()">
      <div class="selectText">{{selectName}}</div>
      <div class="downIcon"></div>
    </div>
    <!--    下拉框内容-->
    <!--    遮罩-->
    <div class="mask" v-show='showSelect' @click="hideSelect()"></div>
    <div class="selectList" v-show='showSelect'>
      <div class="upIcon"></div>
      <div class="selectItem">
        <div class="text" v-for="(item,index) in selectList" :key="index" @click="getSelectVal(item.name,item.value)">{{item.name}}</div>
      </div>
    </div>
    <div class="accountList">
      <div class="accountItem">
        <div class="iconImg"></div>
        <div class="detailCon">
          <div class="accountTitle">每日任务</div>
          <div class="time">2019-07-25 12:00:46</div>
        </div>
        <div class="coinCon">
          <div class="coin"></div>
          <div class="coinCount">+20金币</div>
        </div>
      </div>

      <div class="accountItem">
        <div class="iconImg"></div>
        <div class="detailCon">
          <div class="accountTitle">每日任务</div>
          <div class="time">2019-07-25 12:00:46</div>
        </div>
        <div class="coinCon">
          <div class="coin"></div>
          <div class="coinCount">+200000金币</div>
        </div>
      </div>

    </div>
    <div class="footerTips">已经到底了</div>
    
    
  </div>
</template>

<script>
    export default {
      name: "AccountDetail",
      data(){
        return {
          showSelect :false,
          selectName : '全部',
          selectList : [
            {
              name : '全部',
              value : 1
            },
            {
              name : '今日',
              value : 2
            },
            {
              name : '昨日',
              value : 3
            },
            {
              name : '最近7天',
              value : 4
            },
            {
              name : '最近14天',
              value : 5
            },
            {
              name : '最近30天',
              value : 6
            },
          ]
        }
      },
      methods: {
        //获取点击的下拉框value
        getSelectVal : function(name,value) {
          console.log('获取到的value', value);
          this.selectName = name;
          this.hideSelect();
        },
        //下拉显示
        select : function() {
          this.showSelect = true;
        },
        //下拉隐藏
        hideSelect : function () {
          this.showSelect = false;
        }
      }
    }
</script>

<style lang="less" scoped>
  .myTaskCon{
    width: 100%;
    height: 100%;
    background: #313340;
  }
  .backIcon{
    width: 48px;
    height: 48px;
    position: absolute;
    top: 26px;
    left: 24px;
    .bg-image("~index/assets/back");
  }
  .bg{
    width: 100%;
    height: 160*2px;
    .bg-image("~index/assets/mine/account/bg");
    background-position: 0px -36px;
  }
  .select{
    width: 108*2px;
    height: 28*2px;
    margin-top: 32px;
    margin-left: 255*2px;
    margin-bottom: 32px;
    border-radius: 28px;
    border: 2px solid #DDB67F;
    font-size: 28px;
    line-height: 28*2px;
  }
  .selectText{
    width: 72*2px;
    margin-left: 16px;
    float: left;
    color: #DDB67F;
    text-align: center;
  }
  .downIcon{
    width: 24px;
    height: 24px;
    .bg-image("~index/assets/mine/account/down");
    float: left;
    margin-left: 8px;
    margin-top: 16px;
  }
  .mask{
    width: 100%;
    height: 100%;
    background: #000;
    opacity: .4;
    z-index: 99;
    position: absolute;
    top: 0;
    left: 0;
  }
  .selectList{
    width: 108*2px;
    height: 480px;
    background: #313340;
    position: absolute;
    right: 24px;
    top: 215*2px;
    z-index: 999;
  }
  .upIcon{
    width: 24px;
    height: 8px;
    .bg-image("~index/assets/mine/account/upIcon");
    position: absolute;
    right: 24px;
    top: -8px;
  }
  .selectItem{
    width: 100%;
    height: 80px;
    border-bottom: 2px solid #21232F;
  }
  .selectItem .text{
    width: 68*2px;
    height: 80px;
    line-height: 80px;
    font-size: 28px;
    text-align: center;
    color: #DDB67F;
    margin-left: 24px;
  }
  .accountList{
    width: 100%;
    background: #282A37;
  }
  .accountItem{
    width: 363*2px;
    height: 71*2px;
    margin-left: 24px;
    border-bottom: 2px solid #21232F;
  }
  .iconImg{
    width: 48*2px;
    height: 48*2px;
    margin-top: 24px;
    float: left;
    .bg-image("~index/assets/mine/account/up");
  }
  .detailCon{
    width: 168*2px;
    height: 48*2px;
    margin-top:24px;
    margin-left: 16px;
    float: left;
  }
  .coinCon{
    height: 48px;
    float: right;
    margin-right: 24px;
    margin-top: 24px;
  }
  .accountTitle{
    height: 48px;
    line-height: 48px;
    font-size: 30px;
  }
  .time{
    height: 40px;
    line-height: 40px;
    opacity: .6;
  }
  .coin{
    width: 40px;
    height: 40px;
    .bg-image("~index/assets/mine/coin");
    margin-top: 4px;
    float: left;
  }
  .coinCount{
    height: 48px;
    line-height: 48px;
    font-size: 28px;
    float: left;
    color: rgba(255, 255, 255, 0.8);
  }
  .footerTips{
    width: 100%;
    height: 40px;
    line-height: 40px;
    margin-top: 32*2px;
    text-align: center;
    opacity: .4;
    font-size: 22px;
  }
</style>



